@import 'src/styles/index';

$widget-order: 1;

.user-profile-widget {
  background: $color-white;
  border: 1px solid $color-ghost;
  border-radius: 5px;
  position: absolute;
  right: 1px;
  top: 125%;
  width: 200px;
  z-index: $widget-order;

  &::after,
  &::before {
    border: solid transparent;
    bottom: 100%;
    content: '';
    height: 0;
    left: 90%;
    pointer-events: none;
    position: absolute;
    width: 0;
  }

  &::after {
    border-color: $color-transparent;
    border-bottom-color: $color-white;
    border-width: 7px;
    margin-left: -7px;
  }

  &::before {
    border-color: $color-transparent;
    border-bottom-color: $color-ghost;
    border-width: 9px;
    margin-left: -9px;
  }

  &__button {
    background: transparent;
    border: 0;
    color: $color-fiord;
    cursor: pointer;
    display: flex;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    max-height: 48px;
    padding: 13px 8px;
    text-align: left;
    width: 100%;

    &:hover {
      background-color: $color-porcelain;
    }

    &:last-child {
      border-radius: 0 0 5px 5px;
    }

    &:first-child {
      border-radius: 5px 5px 0 0;
    }
  }

  &__icon {
    height: 17px;
    padding: 3px 9px;
    stroke: $color-fiord;
    stroke-width: 3px;
    width: 17px;
  }
}
